/* Style the video: 100% width and height to cover the entire window */
#myVideo {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 300;
    bottom: 0;
    z-index: -1;
    -moz-transform:scale(2);
    -webkit-transform:scale(2);
    -o-transform:scale(2);
    -ms-transform:scale(2);
    transform:scale(2);
  }

